body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: gray;
  display: flex;
  justify-content: center; }

.container-clock {
  width: 90vw;
  margin: .5em .5em .5em 1em;
  padding: 1em;
  background-color: white;
  border: 3px solid black;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .container-clock .header-row {
    padding-bottom: .5em; }
  .container-clock .settings-row {
    justify-content: space-between;
    padding-bottom: .5em; }
    .container-clock .settings-row #session-label {
      padding: 0 .5em; }
    .container-clock .settings-row #break-label {
      padding: 0 .5em; }
    @media screen and (max-width: 600px) {
      .container-clock .settings-row #timer-label {
        text-align: center; } }
  .container-clock .display-row {
    padding-bottom: .5em; }

@media screen and (min-width: 600px) {
  .container-clock {
    width: 50vw;
    margin: 3em; }
    .container-clock .settings-row #timer-label {
      padding-left: 4em; } }

@media screen and (min-width: 900px) {
  .container-clock {
    width: 40vw; } }

@media screen and (min-width: 1200px) {
  .container-clock {
    width: 30vw;
    margin: 5em; } }

@media screen and (min-width: 1800px) {
  .container-clock {
    width: 20vw; } }

